import React, { Component, Fragment, createRef } from "react";

import {
  Card,
  Icon,
  Image,
  Transition,
  Grid,
  Divider,
  Modal,
  Button,
  Loader,
  Segment,
  Dimmer,
  Responsive,
  SegmentGroup,
  Form,
  Sidebar,
  Menu,
  Ref,
  Visibility
} from "semantic-ui-react";
import seducingImages from "../../../strings/SIURL";

export default class Home extends Component {
  constructor(props) {
    super(props);
    this.state = {
      visible: false,
      loading: false,
      showCharacterModal: false,
      showPasswordModal: false,
      modalImage: "",
      enteredPass: "",
      passCorrect: true,
      imagesToBeShown: seducingImages.slice(0, 10),
      cantLoadMore: false,
      currentLength: 10,
      topVisible: true,
      showSidebarOpenButton: false,
      showSideBar: false
    };
    this.contextRef = createRef();
    this.onClick = this.onClick.bind(this);
    this.onModalRequest = this.onModalRequest.bind(this);
    this.handleModalClose = this.handleModalClose.bind(this);
    this.onChangePass = this.onChangePass.bind(this);
    this.onPassClose = this.onPassClose.bind(this);
    this.onPassCheck = this.onPassCheck.bind(this);
    this.onLoadMore = this.onLoadMore.bind(this);
    this.handleMenu = this.handleMenu.bind(this);
    this.handleUpdate = this.handleUpdate.bind(this);
    this.handleHide = this.handleHide.bind(this);
    this.handleOpenSide = this.handleOpenSide.bind(this);
  }
  handleHide(e) {
    this.setState({ showSideBar: false });
  }
  handleOpenSide(e) {
    this.setState({ showSideBar: true });
  }
  onClick(e) {
    this.setState(state => ({
      showPasswordModal: true,
      passCorrect: true
    }));
  }
  onModalRequest(e) {
    this.setState({ showCharacterModal: true, modalImage: `${e.target.src}` });
  }
  handleModalClose() {
    this.setState({ showCharacterModal: false, modalImage: `` });
  }
  onChangePass(e) {
    this.setState({ [e.target.name]: e.target.value, passCorrect: true });
  }
  onPassClose(e) {
    this.setState({ showPasswordModal: false });
  }
  handleMenu(e, { name }) {
    this.props.history.push("/home");
  }
  onPassCheck(e) {
    let password = "helloguys";
    this.setState(state => ({
      passCorrect: password === state.enteredPass,
      showPasswordModal: !(password === state.enteredPass),
      loading: password === state.enteredPass
    }));
    setTimeout(() => {
      this.setState(state => ({ visible: state.passCorrect, loading: false }));
    }, 1000);
  }
  onLoadMore(e) {
    console.log("hello");

    if (seducingImages.length - this.state.currentLength > 5) {
      return this.setState(state => ({
        imagesToBeShown: state.imagesToBeShown.concat([
          seducingImages[state.currentLength + 1],
          seducingImages[state.currentLength + 2],
          seducingImages[state.currentLength + 3],
          seducingImages[state.currentLength + 4],
          seducingImages[state.currentLength + 5]
        ]),
        currentLength: state.currentLength + 5,
        cantLoadMore: state.currentLength >= seducingImages.length
      }));
    }
    return this.setState({ cantLoadMore: true });
  }
  handleUpdate(e, { calculations }) {
    this.setState(state => ({
      topVisible: calculations.topVisible,
      showSidebarOpenButton:
        calculations.width < 1756 && !calculations.topVisible
    }));
  }
  render() {
    let imagesCard = this.state.imagesToBeShown.map(url => (
      <Card
        key={url}
        style={{
          border: "none",
          boxShadow: "0px 10px 10px lightgrey",
          width: 650
        }}
      >
        <div style={{ overflow: "hidden" }}>
          <div onClick={this.onModalRequest} className="reblur">
            <Image src={url} centered></Image>
          </div>
        </div>
        <Card.Content>
          <Card.Header>Dead Or Alive 6</Card.Header>
          <Card.Meta>Sensitive Content Click To View It Any Way</Card.Meta>
        </Card.Content>
      </Card>
    ));

    let cardsGroup = this.state.loading ? (
      ""
    ) : (
      <Card.Group centered itemsPerRow="1">
        {imagesCard}
      </Card.Group>
    );

    return (
      <div>
        <Visibility onUpdate={this.handleUpdate}></Visibility>
        <Transition
          visible={this.state.showSidebarOpenButton}
          animation="fade down"
          duration={200}
        >
          <Button
            style={{
              position: "fixed",
              bottom: 20,
              left: 20,
              zIndex: 100
            }}
            color="red"
            onClick={this.handleOpenSide}
          >
            <Button.Content>Menu</Button.Content>
          </Button>
        </Transition>
        <Sidebar
          as={Menu}
          animation="push"
          direction="left"
          icon="labeled"
          inverted
          onHide={this.handleHide}
          vertical
          visible={this.state.showSideBar}
          width="wide"
        >
          <Menu.Header>
            <Image
              circular
              size="tiny"
              centered
              src={require("../../../images/complainify.png")}
            ></Image>
          </Menu.Header>
          <Menu.Item
            as="a"
            name="home"
            style={{ marginBottom: 10, marginTop: 10 }}
            onClick={this.handleMenu}
            header
          >
            Home
          </Menu.Item>
          <Menu.Item active as="a" header style={{ marginBottom: 10 }}>
            Adult
          </Menu.Item>
          <Menu.Item as="a" header onClick={this.handleMenu}>
            About
          </Menu.Item>
          <Button
            style={{ marginTop: 10 }}
            color="red"
            onClick={this.handleHide}
            inverted
          >
            Close
            <Icon style={{ marginLeft: 10 }} centered name="close"></Icon>
          </Button>
        </Sidebar>
        <Transition
          visible={!this.state.topVisible && !this.state.showSidebarOpenButton}
          animation="fade down"
          duration={300}
        >
          <Menu
            secondary
            vertical
            pointing
            size="huge"
            style={{ position: "fixed", left: "10%", top: "0%", zIndex: 100 }}
          >
            <Menu.Item header>
              <div
                style={{
                  marginLeft: 10,
                  fontFamily: "poppins",
                  fontWeight: "bold"
                }}
              >
                Anonimity
              </div>
            </Menu.Item>
            <Menu.Item as="a" onClick={this.handleMenu}>
              Home
            </Menu.Item>
            <Menu.Item as="a" active>
              Adult
            </Menu.Item>
            <Menu.Item as="a" onClick={this.handleMenu}>
              About
            </Menu.Item>
          </Menu>
        </Transition>

        <Menu
          secondary
          stackable
          size="huge"
          style={{ marginLeft: "20%", marginBottom: "0%", marginRight: "20%" }}
        >
          <Menu.Item header>
            <Image
              src={require("../../../images/complainify.png")}
              size="tiny"
              rounded
            ></Image>
            <div
              style={{
                marginLeft: 10,
                fontFamily: "poppins",
                fontWeight: "bold"
              }}
            >
              Anonimity
            </div>
          </Menu.Item>
          <Menu.Item as="a" header onClick={this.handleMenu}>
            Home
          </Menu.Item>
          <Menu.Item as="a" header active>
            Adult
          </Menu.Item>
          <Menu.Item as="a" header onClick={this.handleMenu}>
            About
          </Menu.Item>
        </Menu>

        <Modal
          open={this.state.showPasswordModal}
          basic
          onClose={this.handlePasswordModalClose}
        >
          <Form>
            <Form.Field>
              <Transition
                visible={this.state.passCorrect}
                animation={`${!this.state.passCorrect ? "shake" : ""}`}
                duration={500}
              >
                <input
                  value={this.state.enteredPass}
                  name="enteredPass"
                  onChange={this.onChangePass}
                  style={{
                    backgroundColor: "transparent",
                    border: `1px solid ${
                      this.state.passCorrect ? "grey" : "red"
                    }`,
                    color: "white"
                  }}
                  placeholder="Enter Password"
                  type="password"
                ></input>
              </Transition>
            </Form.Field>
            <Button color="green" onClick={this.onPassCheck} inverted>
              Enter
            </Button>
            <Button color="red" onClick={this.onPassClose} inverted>
              Cancel
            </Button>
          </Form>
        </Modal>
        <Transition visible={this.state.showCharacterModal} duration={500}>
          <Modal
            open={this.state.showCharacterModal}
            onClose={this.handleModalClose}
            basic
            size="large"
            dimmer="blurring"
          >
            <Modal.Content scrolling image>
              <Image
                wrapped
                centered
                as="a"
                target="_blank"
                href={this.state.modalImage}
                src={this.state.modalImage}
              />
            </Modal.Content>

            <Modal.Actions>
              <Button color="red" inverted onClick={this.handleModalClose}>
                Close
              </Button>
            </Modal.Actions>
          </Modal>
        </Transition>
        <Dimmer active={this.state.loading}>
          <Loader></Loader>
        </Dimmer>
        <Sidebar.Pushable>
          <Divider></Divider>

          {this.state.visible ? (
            ""
          ) : (
            <div>
              <Button color="black" animated onClick={this.onClick}>
                <Button.Content visible>Show Me Heaven</Button.Content>
                <Button.Content hidden>
                  <Icon name="arrow right" />
                </Button.Content>
              </Button>
            </div>
          )}
          <SegmentGroup style={{ border: "none" }}>
            <Ref innerRef={this.contextRef}>
              <Responsive
                as={Segment}
                style={{ border: "none" }}
                {...Responsive.onlyComputer}
              >
                <Fragment>
                  {this.state.visible ? (
                    <Grid centered divided>
                      <Grid.Column width="3"></Grid.Column>

                      <Grid.Column width="10">
                        {this.state.visible ? cardsGroup : ""}
                      </Grid.Column>
                      <Grid.Column width="3"></Grid.Column>
                    </Grid>
                  ) : (
                    ""
                  )}
                </Fragment>
              </Responsive>
            </Ref>

            <Responsive
              as={Segment}
              style={{ border: "none" }}
              {...Responsive.onlyTablet}
            >
              <Fragment>
                <Grid divided>
                  <Grid.Column width="3"></Grid.Column>
                  <Grid.Column width="10">
                    {this.state.visible ? cardsGroup : ""}
                  </Grid.Column>
                  <Grid.Column width="3"></Grid.Column>
                </Grid>
              </Fragment>
            </Responsive>
            <Responsive
              as={Segment}
              style={{ border: "none" }}
              {...Responsive.onlyMobile}
            >
              <Fragment>
                <Grid>
                  <Grid.Column width="10">
                    {this.state.visible ? cardsGroup : ""}
                  </Grid.Column>
                </Grid>
              </Fragment>
            </Responsive>
            {this.state.visible ? (
              <Button
                style={{ marginBottom: 10 }}
                disabled={this.state.cantLoadMore}
                onClick={this.onLoadMore}
              >
                {this.state.cantLoadMore ? "Can't Load More" : "Load More"}
              </Button>
            ) : (
              ""
            )}
          </SegmentGroup>
        </Sidebar.Pushable>
      </div>
    );
  }
}
